.classic-menu h3{
    font-size: 32px;
    line-height: 48px;
    font-weight: 500;
    text-align: center;
}
.classic-menu h3 a{
    display: block;
    padding: 24px 0  16px;
    width: 432px;
    border: 1px solid #DCDCDC;
    border-bottom: 8px solid #fff;

}
.classic-menu ul{
    display: flex;
    justify-content: space-between;
}
.classic-menu h3 a:hover{
    border-bottom: 8px solid #AA0601;
}